nav {
  ul a, .brand-logo {
    color: #444; } }

p {
  line-height: 2rem; }

.button-collapse {
  color: #26a69a; }

.parallax-container {
  min-height: 380px;
  line-height: 0;
  height: auto;
  color: rgba(255, 255, 255, 0.9);
  //box-shadow: inset -5px 40px 100px 0 rgba(51, 51, 51, 0.25), inset 5px -40px 100px 0 rgba(51, 51, 51, 0.25)
  .section {
    width: 100%; } }

@media only screen and (max-width: 992px) {
  #index-banner .section {
    top: 10%;
    bottom: 10%; } }

@media only screen and (max-width: 600px) {
  #index-banner .section {
    top: 0; } }

.icon-block {
  padding: 0 15px;
  .material-icons {
    font-size: inherit; } }

#logo-container {
  height: 1.5em;
  padding-top: 0.25em;
  img {
    height: 100%;
    max-width: 100%; } }

#header-logo-container {
  img {
    //max-height: 10em
    max-width: 30em;
    width: 100%;
    margin-bottom: 3em; } }

.image-blur {
  -webkit-filter: blur(4px);
  filter: blur(4px); }

.image-blur-less {
  -webkit-filter: blur(2px);
  filter: blur(2px); }

#header-container {
  padding-top: 5em;
  padding-bottom: 5em; }

#product-family .product-family-header {
  background: white;
  padding: 0 5px 0 5px;
  img {
    height: auto;
    width: 100%;
    transition: all ease-in-out 100ms; }
  img:hover {
    box-shadow: 0 10px 20px -7px #e0e0e0;
    transform: translate(0px, -5px); } }

.modal-product-image {
  max-width: 100%; }

#modals {
  .modal-content {
    .inline-right-30 {
      float: right;
      max-width: 30%; }
    .inline-right-40 {
      float: right;
      max-width: 40%; }
    .inline-right-50 {
      float: right;
      max-width: 50%; }
    .inline-left-30 {
      float: left;
      max-width: 30%; }
    .inline-left-40 {
      float: left;
      max-width: 40%; }
    .inline-left-50 {
      float: left;
      max-width: 50%; } } }

.bold {
  font-weight: bold; }

.margin-bottom-5em {
  margin-bottom: 5em; }

.input-field input[type=text]:focus {
  + label {
    color: #fe1109; }
  border-bottom: 1px solid #fe1109;
  box-shadow: 0 1px 0 0 #fe1109; }

.input-field input[type=email]:focus {
  + label {
    color: #fe1109; }
  border-bottom: 1px solid #fe1109;
  box-shadow: 0 1px 0 0 #fe1109; }

.input-field textarea[type=text]:focus {
  + label {
    color: #fe1109; }
  border-bottom: 1px solid #fe1109;
  box-shadow: 0 1px 0 0 #fe1109; }

form {
  .error {
    font-size: small;
    color: #fe1109; } }

#footer-logo {
  max-height: 2em;
  width: auto; }

.footer-phone {
  width: 0.75em;
  text-align: center;
  display: inline-block; }
